.hover-color {
  filter: grayscale(100%);
  opacity: .5;
  transition: filter .5s ease-out, transform .25s ease-out;
}
.hover-color:hover {
  filter: none;
  opacity: 1;
  transition: filter .5s ease-out, transform .25s ease-out;
}

.schedule {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 1rem;
}
.schedule-item {
  flex: 1 1 auto;
  flex-basis: 20rem;
}
